Refresh</div>\n    <!-- <diV v-if=\"artifacts.length > 0\">\n        <div v-for=\"artifact in artifacts\" style=\"display:flex\" @click=\"downloadArtifact(artifact)\"\n            :title=\"`Download: ${artifact.name}`\">\n            <i class=\"mdi mdi-monitor artifactIcons\"></i> {{ artifact.type }}\n        </div>\n    </diV> -->\n    <div v-for=\"step in steps\" :key=\"step.ID\" v-bind:value=\"step.ID\">\n        <div data-bs-toggle=\"collapse\" role=\"button\" aria-expanded=\"false\" aria-controls=\"multiCollapseExample2131\"\n           >\n            <div class='expandContent' @click=\"chooseStep(step.ID)\">\n                <i   style='font-size: 16px;' :class=\"step.isOpen ? 'mdi mdi-chevron-down' : 'mdi mdi-chevron-right'\"></i>\n                <i :style=\"`color:${getStatusIconColor(step.status)}; font-size: 16px; padding-left: 15px;`\"\n                    :class='getStatusIconClass(step.status)'></i>{{ step.name }}\n            </div>\n\n            <div class=\"logContent\" v-if=\"step.isOpen\">\n                <pre>\n                    <div v-for=\"log in step.logs\">{{ log }}</div>\n                </pre>\n            </div>\n        </div>\n    </div>\n    <div class=\"infoText\"  style=\"display:flex\">\n        <div class='col-10 ' style=\"text-align:end\">\n                  <div style=\"padding-top: 8px;\">Download The Artifacts of Job</div>\n        </div>\n        <div class=\"dropdown col-2\" style=\"text-align:end\">\n  \n            <button @click=\"artifactList\" class=\" artifactDownloadBtn\"><i class='mdi mdi-tray-arrow-down'\n                    style=\"padding-right: 8px;\"></i>DOWNLOAD <i class='mdi mdi-chevron-down'></i></button>\n            <div v-if=\"artifactListVisible\" v-for=\"artifact in artifacts\" class=\"dropdown-content\">\n                <span @click=\"downloadArtifact(artifact)\"  class='artifactListItem'><i :class=\"computedArtifact(artifact.type)\"></i>{{ artifact.type\n                }}</span>\n            </div>\n        </div>\n    </div>\n\n\n</template>\n\n<script setup lang=\"ts\">import { container } from 'tsyringe';\nimport { onMounted, onUnmounted, ref } from 'vue';\nimport 'bootstrap/js/dist/collapse'\nimport { IExportJobArtifactInfoItem, IExportJobItem, IExportJobStepItem } from '../../../../domain/model/models';\nimport { IListExportJobStepLogs } from '../../../../domain/useCase/IListExportJobStepLogs';\nimport { IListExportJobSteps } from '../../../../domain/useCase/IListExportJobSteps';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModelManager } from '../../../../domain/viewModel/IViewModelManager';\nimport { IListExportJobArtifacts } from '../../../../domain/useCase/IListExportJobArtifacts';\nimport { IConfig } from '../../../../domain/infrastructure/IConfig';\nimport { INetwork } from '../../../../domain/infrastructure/INetwork';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport { createError } from '../../../../domain/model/shellError';\nimport { StatusType } from '@stechquick/algae/lib/qCloudTemp/exporter';\ninterface IExtentedStepItem extends IExportJobStepItem {\n    isOpen?: boolean\n}\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst config = container.resolve<IConfig>(IConfig);\nconst network = container.resolve<INetwork>(INetwork);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\n\nconst props = defineProps<{ job: IExportJobItem}>();\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst steps = ref<Array<IExtentedStepItem>>([]);\nconst artifactListVisible =ref(false)\nconst artifacts = ref<Array<IExportJobArtifactInfoItem>>([]);\n\nfunction getData() {\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobSteps>(IListExportJobSteps, { jobID: props.job.jobID });\n        const currentJob = viewModelManager.findObject(props.job.jobID) as IExportJobItem\n        steps.value = currentJob.steps!\n        if (props.job.status == 'success') {\n            await executor.executeUseCase<IListExportJobArtifacts>(IListExportJobArtifacts, { jobID: props.job.jobID });\n            artifacts.value = currentJob.artifacts!\n        }\n\n    }, { loading: true });\n}\ndefineExpose({\n  getData,\n});\n\ngetData();\n\nfunction getStatusIconColor(status: StatusType) {\n    switch (status) {\n        case \"success\": return \"#149911\";\n        case \"failed\": return \"red\";\n        case \"running\": return \"blue\";\n        case \"waiting\": return \"grey\";\n    }\n}\n\nfunction getStatusIconClass(status: StatusType) {\n    switch (status) {\n        case \"success\": return \"mdi mdi-check-circle-outline\";\n        case \"failed\": return \"mdi mdi-close-circle-outline\";\n        case \"running\": return \"mdi mdi-progress-upload\";\n        case \"waiting\": return \"mdi mdi-timer-sand\";\n    }\n}\n\nfunction chooseStep(stepID: string) {\n    const currentStep = steps.value.find(i => i.ID == stepID)!;\n    if (currentStep.isOpen) {\n        currentStep.isOpen = false;\n        return;\n    }\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobStepLogs>(IListExportJobStepLogs, { stepID });\n        currentStep.isOpen = true;\n    }, { loading: true });\n}\nfunction computedArtifact(type: string) {\n    if (type == 'web') {\n        return 'mdi mdi-monitor artifactIcons'\n    }\n}\n\nfunction getArtifactLink(artifact: IExportJobArtifactInfoItem) {\n    const token = viewModel.user!.authorization;\n    const { objectName } = artifact.details;\n    const url = config.getValue(\"providerApiUrl\");\n    return network.join(url, `/minio/getExportJobArtifact?objectName=${objectName}&t=${token}`);\n}\n\nfunction artifactList(){\n        if(artifactListVisible.value){\n        artifactListVisible.value=false\n    }\n    else{\n        artifactListVisible.value=true\n    }\n}\nfunction downloadArtifact(artifact: IExportJobArtifactInfoItem) {\n\n    const a = document.createElement(\"a\");\n    a.href = getArtifactLink(artifact);\n    a.target = \"new\";\n    a.download = \"download\";\n    document.body.appendChild(a);\n    a.click();\n    document.body.removeChild(a);\n    console.log('dada')\n}\nconst messageHandler: (ev: MessageEvent) => void = (ev) => {\n    executor.execute(async () => {\n        let message: { downloaderTab: boolean, error: string, stack: string } | undefined = ev.data;\n        if (!message || !message.downloaderTab) { return; }\n\n        throw createError({ message: message.error, type: \"technical\" });\n    }, { loading: false });\n};\nonMounted(() => window.addEventListener(\"message\", messageHandler));\nonUnmounted(() => window.removeEventListener(\"message\", messageHandler));\n\n</script>\n\n\n<style scoped>\n.expandContent:hover {\n    background-color: #151233;\n    color: white\n}\n\n.artifactDownloadBtn {\n    background-color: #151233;\n    width: 138px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    color: white;\n}\n\n.dropbtn {\n    background-color: #04AA6D;\n    color: white;\n    padding: 16px;\n    font-size: 16px;\n    border: none;\n}\n\n.dropdown {\n    position: relative;\n    display: inline-block;\n}\n\n.dropdown-content {\n    display: block;\n    position: absolute;\n    background-color: #f1f1f1;\n    text-align: end;\n    min-width: 140px;\n    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n    z-index: 1;\n    right: 0px;\n    text-align: start;\n}\n\n.dropdown-content span {\n    color: black;\n    padding: 12px 16px;\n    text-decoration: none;\n    display: block;\n}\n\n.dropdown-content span:hover {\n    background-color: #E3E3E3;\n}\n\n.artifactIcons {\n    padding-right: 8px;\n}\n.infoText{\n    border-top: 1px solid #E3E3E3;\n    padding-top: 20px;\n    padding-bottom: 20px;\n    padding-right: 20px;\n    margin-top: 10px;\n}\n</style>"],"sourceRoot":""}]);
